$text_color: #333;
$corner_radius: 4px;
$menu_text_color: darkslategray;
$title_color: teal;


body {
    font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, sans-serif;
}

a {
  color: cornflowerblue;
  background: transparent;
  word-wrap: break-word;
  word-break: break-all;
  text-decoration: none;
}

a:active, a:hover {
    color: tomato;
    outline: 1;
}

li a{
    color: $menu_text_color;
}

li a:active, a:hover{
    color: tomato;
}


h1, h2, h3, h4, h5, h6 {
    color: $title_color;
    font-family: Monaco, Consolas, "Andale  Mono", "DejaVu Sans Mono", monospace;
    padding-top: 1em;
    padding-bottom: 1em;
}

nav {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0%);
}

nav ul {
    width: 500px;
    font-size: 18px;
    list-style-type: none;
    font: Monaco, Consolas, "Andale  Mono", "DejaVu Sans Mono", monospace;
}

nav li {
    width: 200px;
    float: left;
    padding: 0% 5% 0% 5%;
}

nav a {
    padding: $corner_radius;
    border-radius: $corner_radius; 
    -moz-border-radius: $corner_radius; 
    -webkit-border-radius: $corner_radius; 
    display: block;
    text-align: center;
}

.current {
    border: 1px solid #BCBEC0;
}

h1 {
    text-align: center;
    font-size: 2.0;
    margin-bottom: 16px;
}

h2 {
    font-size: 1.5em;
    &:before { content: "## " }
}

h3 {
  font-size: 1.25em;
  &:before { content: "### " }
}

h4 {
  font-size: 1.10em;
  line-height: 1.2;
  &:before { content: "#### " }
}

h5 {
  font-size: 1em;
  &:before { content: "##### " }
}

h6 {
  font-size: 1em;
  color: #777;
}

code {
    border-radius: $corner_radius; 
    -moz-border-radius: $corner_radius; 
    -webkit-border-radius: $corner_radius; 
    border: 1px solid #BCBEC0;
    padding: 2px;
    font:12px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    line-height: 2;
    color: teal;
}

blockquote {
    position: relative;
    padding-left: 1em;
    border-left: 0.2em solid teal;
    font:16px Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    font-weight: 100;
    &:before, &:after {
        content: '\201C';
        font-family: 'Sanchez';
        color: teal;
    }
    &:after { content: '\201D'; }
}

.menu_item{
    margin-left: 18px;
    margin-bottom: 18px;
    font-size: 1.2em;
    line-height: 1.0;
    font-family: Monaco, Consolas, "Andale  Mono","DejaVu Sans Mono", monospace;
}

.main{
    top: 80px;
    overflow: auto;
}

.column {
    float: left;
}

.left {
    width: 20%;
    margin-right: 5%;
}

.right {
    width: 70%;
}

.fig_left {
  width: 45%;
  margin-right: 5%;
}

.fig_right {
  width: 45%;
  margin-left: 5%;
}

.intro{
    font-size: 14px;
    line-height: 1.5;
    padding: 12px;
    text-align: center;
    color: $text_color;
    font-family: monospace;
    border-bottom: 1px solid #BCBEC0;
}

.post {
    color: $text_color;
    font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, sans-serif;
    font-size: 16px;
    line-height: 1.5;
    min-width: 800px;
}

.centre{
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0%);
}

.catalog {
    min-width: 800px;
    list-style-type: none;
}

img {
    padding: 5%;
    text-align: center;
    width: 60%;
}

#side_bar li{
    margin: 0;
    padding: 0;
    list-style-type: none;
}

#side_bar ul{
    margin: 0;
    padding: 0%;
    padding-left: 10%;
    list-style-type: none;
}

